@import url(https://fonts.googleapis.com/css?family=Roboto:100,300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
h1 {
    font-family: 'Roboto', sans-serif;
    font-size: 110px;
    font-weight:100; 
    letter-spacing: 30px;

}

h3 {
    margin: 0;
    font-family: 'Roboto', sans-serif;
    font-weight:300; 
    line-height:24px;

}
#space{
    margin: auto;
    width: 800px; 
    height: 500px;
    -webkit-transform: perspective 400px;
            transform: perspective 400px;
    -webkit-transform-origin: 110px 110px;
            transform-origin: 110px 110px; 

}

.elogo{
    margin: auto;
    position: reative;
    width: 400px;
    height: 400px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform-origin: 110px 110px 0;
            transform-origin: 110px 110px 0;
    -webkit-transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);
            transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);

}
.elogo:hover {
    -webkit-animation: aniether 6s infinite;
            animation: aniether 6s infinite;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform-origin: 110px 110px 0;
            transform-origin: 110px 110px 0;
    -webkit-transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);
            transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);

}

.trif {
    position: absolute;
    width: 0px;
    height: 0px;
    border-left: 100px solid transparent; 
    border-right: 100px solid transparent;

}
.elogo .u1 {
    -webkit-transform: translateZ(50px) rotateX(23.5deg);
            transform: translateZ(50px) rotateX(23.5deg);  
    border-bottom: 250px solid rgba(50,50,51,0.8);

}
.elogo .u2 { 
    -webkit-transform: rotateY(90deg) translateZ(50px) rotateX(23.5deg); 
            transform: rotateY(90deg) translateZ(50px) rotateX(23.5deg);
    border-bottom: 250px solid rgba(50,50,51,0.6);

}
.elogo .u3 { 
    -webkit-transform: rotateY(180deg) translateZ(50px) rotateX(23.5deg); 
            transform: rotateY(180deg) translateZ(50px) rotateX(23.5deg); 
    border-bottom: 250px solid rgba(50,50,51,0.3);

}
.elogo .u4 { 
    -webkit-transform: rotateY(-90deg) translateZ(50px) rotateX(23.5deg); 
            transform: rotateY(-90deg) translateZ(50px) rotateX(23.5deg); 
    border-bottom: 250px solid rgba(50,50,51,0.3);

}

.elogo .ct { 
    width: 200px;
    height: 200px;
    border: 0;
    -webkit-transform: rotateX(-90deg) translateZ(140px);
            transform: rotateX(-90deg) translateZ(140px);
    background-color: rgba(60,60,61,1);

}
.elogo .l1 { 
    -webkit-transform: translateY(55px) translateZ(50px) rotateX(156.5deg); 
            transform: translateY(55px) translateZ(50px) rotateX(156.5deg);
    border-bottom: 250px solid rgba(50,50,51,0.85);

}
.elogo .l4 { 
    -webkit-transform: translateY(55px) rotateY(-90deg) rotateZ(0deg) translateZ(50px) rotateX(156.5deg); 
            transform: translateY(55px) rotateY(-90deg) rotateZ(0deg) translateZ(50px) rotateX(156.5deg); 
    border-bottom: 250px solid rgba(50,50,51,0.45);

}

@-webkit-keyframes aniether {
    20%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    30%      {-webkit-transform: perspective(400px) rotateY(10deg);transform: perspective(400px) rotateY(10deg); }
    50%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    60%      {-webkit-transform: perspective(400px) rotateY(80deg);transform: perspective(400px) rotateY(80deg); }
    80%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    100%      {-webkit-transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg); }

}

@keyframes aniether {
    20%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    30%      {-webkit-transform: perspective(400px) rotateY(10deg);transform: perspective(400px) rotateY(10deg); }
    50%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    60%      {-webkit-transform: perspective(400px) rotateY(80deg);transform: perspective(400px) rotateY(80deg); }
    80%      {-webkit-transform: perspective(400px) rotateY(46deg);transform: perspective(400px) rotateY(46deg); }
    100%      {-webkit-transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg);transform: perspective(400px) rotateY(46deg) rotateX(-5deg) rotateZ(-5deg); }

}

